<template>
  <div>
    <div class="search">
      <input v-model="keyword" class="search-input" type="text" placeholder="输入城市名或拼音"/>
    </div>
    <div class="search-content"
         ref="search"
         v-show="keyword">
      <ul>
        <li class="search-item border-bottom"
            v-for="item of list"
            :key="item.id"
            @click="handleCityClick(item.name)">
          {{item.name}}
        </li>
        <li class="search-item border-bottom" v-show="hasData">
          没有找到匹配数据
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data(){
    return{
      keyword: '',
      list: [],
      timer: null
    }
  },
  methods:{
    handleCityClick(city){
      this.$store.commit('changeCity', city);
      this.$router.push('/')
    }
  },
  watch: {
    keyword() {
      if(this.timer) {
        clearTimeout(this.timer)
      }
      if(!this.keyword){
        this.list = [];
        return
      }
      this.timer = setTimeout(() => {
        const result = [];
        for(let i in this.cities){
          this.cities[i].forEach((value) => {
            if(value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1){
              result.push(value)
            }
            }

          )
        }
        this.list = result
      }, 100)
    }
  },
  mounted (){
    this.scroll = new Bscroll(this.$refs.search)
  },
  computed:{
    hasData(){
      return !this.list.length
    }
  }
}
</script>

<style lang='scss' scoped type="text/scss">
  @import '~styles/_varibles.scss';
  .search{
    padding: 0 .1rem;
    height: .72rem;
    background-color: $bg-color;
    .search-input{
      box-sizing: border-box;
      padding: 0 .1rem;
      width: 100%;
      height: .62rem;
      line-height: .62rem;
      text-align: center;
      border-radius: .06rem;
      color: #666666;
    }
  }
  .search-content{
    z-index: 1;
    overflow: hidden;
    position: absolute;
    top: 1.58rem;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #eeeeee;
    .search-item{
      line-height: .62rem;
      padding-left: .2rem;
      background-color: #ffffff;
      color: #666666;
    }
  }
</style>



